:host {
  ion-toolbar {
    --background: #fff;
  }
}
.ecl-recommandation_item {
  border-radius: 99px;
  border-top-left-radius: 0;
}

.vehicle-icon-container {
  position: relative;
  overflow: visible;
}

.pitstop-lock-icon {
  position: absolute;
  top: -5px;
  left: -3px;
  font-size: 20px;
  z-index: 1;
  filter: drop-shadow(0 0 2px rgba(255, 255, 255, 0.8));
}

.vehicle-icon-dimmed {
  opacity: 0.7;
  filter: blur(0.5px);
}
